@import "../../scss/vars";

.bookInfo {
  display: flex; 
  flex-direction: row;
  width: 80%;
  height: fit-content;
  border-radius: 20px;
  @include blur-background;
  margin: 50px auto;
  padding: 50px;
  #back {
    @include button-style;
    img {
      width:25px;
    }
    &:hover {
      @include button-hover-style;
    }
  }
  @media (max-width: $mobile-width) {
    flex-direction: column;
    width: 80%;
    padding: 20px;
    margin: 20px 25px 80px;
  }
}
.bookDesc {
  padding: 10px 30px;
  width: 20%;
  img {
    width: 250px;
    margin-top: 40%;
    @media (max-width: $mobile-width) {
      width: 150px;
      margin-left: 55%;
    }
  
  }
}
.bookGeneralInfo {
  width: 35%;
  padding: 40px;
  color: $general-color;
  @media (max-width: $mobile-width) {
    width: 90%;
    padding: 20px 0;
  }
  h2 {
    white-space: pre-wrap;
  }
  p {
    text-align: justify;
  }
}
.bookPrice {
  padding: 40px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.572);
  border-radius: 20px;
  color: $general-color;
  margin-top: 10%;
  input {
    width: 30px;
    @include input-style;
    padding-left: 15px;
  }
  button {
    padding: 10px;
    width: 35%;
    @include button-style;
    margin: 40px 10px 0 180px;
    &:hover {
      @include button-hover-style;
    }
  }
  .row {
    display: flex;
    justify-content: space-between;
  }
}
